<template>
  <div>
    <div class="container-fluid p-0">
      <div class="row d-flex justify-content-center m-0">
        <div
          class="col-12 d-flex justify-content-center quote-block row m-0 p-0"
        >
          <!-- Header -->
          <div
            class="
              col-12
              py-5
              test-title
              px-2
              d-flex
              align-items-center
              justify-content-center
            "
          >
            <h2 class="text-center">{{ shopTitle }}</h2>
          </div>

          <!-- Items -->
          <div
            class="
              col-12 col-md-9
              pb-5
              mb-5
              p-0
              row
              item
              d-flex
              justify-content-between
              align-items-start
            "
            v-for="(item, index) in shopData"
            :key="index"
          >
            <component
              :is="componentMapper[item.slice_type]"
              v-bind="{ item: item, index: index }"
            ></component>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import VideoItem from "@/components/shop/sections/VideoItem.vue";
import StandardItem from "@/components/shop/sections/StandardItem.vue";

export default {
  components: {
    VideoItem,
    StandardItem,
  },

  name: "TestimonialPage",
  data() {
    return {
      shopData: [],
      shopTitle: [],
      componentMapper: {
        video_item: "VideoItem",
        shop_item: "StandardItem",
      },
    };
  },
  methods: {
    getContent() {
      this.$prismic.client.getSingle("shop").then((response) => {
        this.shopData = response.data.body;
        this.shopTitle = response.data.shop_title;
        this.initSnipcart();
      });
    },
    initSnipcart() {
      document.addEventListener("snipcart.ready", () => {
        window.Snipcart.events.on("cart.confirmed", () => {
          console.log('confirmed')
          setTimeout(() => {
            this.$router.push({ path: "/thank-you" });
          }, 2000);
        });
      });
    },
  },

  mounted() {
    this.getContent();
  },
};
</script>

<style scoped>
.call-to-action-title {
  text-decoration: none;
}
h2 {
  color: #212529;
  text-decoration: underline #a77e50;
}
