<template>
  <div data-aos="fade" class="col-12 pb-4 mb-4 white">
    <div
      :class="[
        checkIndex(index) ? 'justify-content-start' : 'justify-content-end',
      ]"
      class="row d-flex m-0 p-0"
    >
      <div class="col-12 col-md-12 col-lg-7 p-0 row">
        <div class="col-12">
          <div class="video-wrapper">
            <iframe
              :src="
                'http://www.youtube.com/embed/' +
                youtubeId +
                '?autoplay=1&amp;html5=1'
              "
              frameborder="0"
              allowfullscreen
            ></iframe>
          </div>
        </div>
        <div class="col-12">
          <figcaption class="blockquote-footer mb-0 mt-2">
            {{ item.primary.credit }}
          </figcaption>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "VideoTestimonial",
  props: {
    item: Object,
    index: Number,
  },
  methods: {
    checkIndex(e) {
      if (e % 2 === 0) {
        return true;
      } else {
        return false;
      }
    },
  },
  computed: {
    youtubeId() {
      let regExp =
        /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
      let match = this.item.primary.video_embed.embed_url.match(regExp);
      return match && match[7].length == 11 ? match[7] : false;
    },
  },
};
</script>

<style>
.video-wrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
}
.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
iframe {
  border-radius: 8px;
}
</style>